<template>
	<div>
		<CContainer class="d-flex align-items-center min-vh-100">
			<CRow class="justify-content-center">
				<CCol md="8">
					<CCardGroup>
						<CCard class="p-4">
							<CCardBody>
								<CForm>
									<h1>登陆</h1>
									<p class="text-muted">Sign In to your account</p>
									<CInput v-model="userLoginName" placeholder="用户名" autocomplete="username email">
										<template #prepend-content>
											<CIcon name="cil-user" /></template>
									</CInput>
									<CInput v-model="password" placeholder="密码" type="password" autocomplete="curent-password">
										<template #prepend-content>
											<CIcon name="cil-lock-locked" /></template>
									</CInput>
									<CRow>
										<CCol col="6">
											<CButton color="primary" class="px-4" v-on:click="fx_login">登 录</CButton>
										</CCol>
										<CCol col="6" class="text-right">
											<CButton color="link" class="px-0">忘记密码?</CButton>
										</CCol>
									</CRow>
								</CForm>
							</CCardBody>

						</CCard>
						<CCard color="primary" text-color="white" class="text-center py-5 d-md-down-none" style="width:44%" body-wrapper>
							<h2>说明</h2>
							<p>
								现在还是模板,coreUI - vue,
								注册登陆的话
								密码随便填一下 123456 就行了.....不要用自己的常用密码
								..................................................................................................................................................................</p>
							<CButton v-on:click="fx_register_btn" color="primary" class="active mt-3">
								注 册!
							</CButton>
						</CCard>
					</CCardGroup>
				</CCol>
			</CRow> 
		</CContainer>
		<TheFooter />
	</div>
</template>

<script>
	import TheFooter from '../../containers/TheFooter.vue'

	export default {
		components: {
			TheFooter
		},
		name: 'Login',
		data() {
			return {
				userLoginName: "",
				// userNickname:"",
				password: "",
				// confirmpassword:"",
				// email:""
			}
		},
		methods: {
			//注册 页面跳转
			fx_register_btn: function(e) {
				this.$router.push({
					name: 'Register'
				});
			},
			fx_login: function(e) {
				//发送 post 请求
				this.$http.post("http://www.adanana.com/app/login", this.$data, {
						emulateJSON: true
					})
					.then(function(res) {
						this.$router.push({name:"Dashboard"});
						// $("#register").modal('hide');
					}, function(res) {
						console.log(res.status);
					}, function(res) {});
			}
		}
	}
</script>
